@media only screen and (min-width: 601px) and (max-width: 1060px) {


  .navbar {

    a {
      min-width: 0px;
      //  background-color: red;
      padding: 10px;
      text-align: left;
    }

    #resume-button-1 {
      font-size: 13px;
    }

    .links {
      font-size: 13px;
      gap: 10px;
    }

  }

  #projects {
    .all_projects {
      .project-card {
        flex-direction: column;
      }
    }
  }

  .gitcont {
    padding: 10px;

    .hh {
      width: 100%;
      margin-bottom: 10px;
      border-radius: 4px;
    }

    .img_div {
      flex-direction: column;
      padding: 0;
      gap: 10px;

      img {
        width: 100%;
        height: auto;
      }
    }
  }
}





//  responsive
@media only screen and (max-width: 600px) {
  .navbar {
    // background-color: red;
    // display: none;

    .links {
      background-color: white;
      position: fixed;
      height: 100vh;
      top: 0;
      padding-top: 80px;
      flex-direction: column;
      width: 50%;
      font-size: 20px;
      gap: 20px;
      right: 0px;
      transition: right 1s;
    }

    .menu-btn {
      display: block;
      position: fixed;
      right: 20px;
      z-index: 9999;
      background: none;
      border: none;
      font-size: 40px;
      top: 10px;
      color: rgb(16, 16, 16);
    }

    #open {
      right: 0px;
    }

    #close {
      right: -100%;
    }

    #resume-button-1 {
      // font-weight: bold;
      font-size: 20px;
      color: white;
      color: rgb(24, 23, 23);
    }
  }

  #home {
    // display: none;
    font-size: 20px;
    height: 100vh;
    flex-direction: column-reverse;
    font-size: 20px;
    justify-content: center;

    .home-img {
      width: 300px;
      height: 300px;
    }
  }

  #projects {
    padding: 20px;

    // display: none;
    .all_projects {
      .project-card {
        flex-direction: column;

        // display: grid;
        // grid-template-columns: 45% 45%;
        .pro-img img {
          max-height: none;
          max-width: 100%;
          height: 200px;
          object-fit: contain;
        }

        .pro-img1 img {
          max-width: 100%;
          height: 200px;
          object-fit: contain;
        }
      }
    }
  }

  #about {
    width: calc(100% - 40px);
    margin: 20px;

    .about_img {
      width: 200px;
      margin-bottom: 30px;
    }

    .about_card {
      flex-direction: column;
      padding: 10px;
      border-radius: 16px;
      text-align: center;
      width: 100%;
    }

    p {
      width: 100%;
      color: white;
      font-family: Raleway, sans-serif;
      text-align: left;
      font-size: 16px;
      line-height: auto;
      padding: 20px;
    }
  }

  // #contact {
  //   // display: none;
  // }

  #skills {
    padding: 10px;

    .skills_deta {
      grid-template-columns: repeat(auto-fit, 160px);
    }

    .skills-card {
      svg {
        font-size: 100px;
      }
    }
  }

  .gitcont {
    // display: none;
    padding: 20px;

    .hh {
      width: 100%;
      margin-bottom: 10px;
      border-radius: 4px;
    }

    .img_div {
      flex-direction: column;
      padding: 0;
      gap: 10px;

      img {
        width: 100%;
        height: auto;
      }
    }
  }
}